/*
 *  Article Thumbnail Object
 *
 *  Version:
 *    1.0.0 - 2021/09/20
 */

.o-article-thumbnail {
  background-color: $s-color--background-subdued;

  &__inner {
    display: flex;
    flex-direction: column;
    text-align: left;
  }

  &__image {
    &-container {
      width: 100%;
      padding-bottom: 116.666%;
    }
  }

  &__content {
    &-main {
      display: block;
    }
  }

  &__title {
    font-weight: 500;
    margin: 1.5625rem 0 0;
  }

  &__info {
    color: $s-color--font-focus;
    display: flex;
    margin: 0.3125rem 0 0;

    &-tag {
      margin: 0 0.3125rem 0 0;
    }

    &-read-time {
      margin-bottom: 0;
    }
  }

  &__desc {
    margin-top: 1rem;
    margin-bottom: 0;
  }

  @include t-media-query($s-medium-up) {
    &__image {
      &-container {
        padding-bottom: 115.38%;
      }
    }

    &__title {
      margin-top: 2.3125rem;
    }

    &__info {
      margin-top: 0.375rem;

      &-tag {
        @include t-font--paragraph-2;
      }

      &-read-time {
        @include t-font--paragraph-2;
      }
    }

    &__desc {
      @include t-font--paragraph-2;
      margin-top: 0.8125rem;
    }
  }
}
