/*
 *  Featured articles
 *    Styles for the featured articles section.
 *
 *  Version:
 *    1.0.0 - 2021/10/22
 */

.c-featured-articles {
  padding: 5.3125rem 0 5.625rem;
  background-color: $s-color--background-focus;

  // Title
  &__title {
    margin: 0 $s-gutter--edge-mobile 1.875rem;
  }

  // Articles
  &__articles {
    display: grid;
    grid-template-columns: repeat(3, #{'min(78.87323943661972%, 21rem)'});
    column-gap: 1.25rem;
    padding: 0 $s-gutter--edge-mobile;
    margin-bottom: 1.4375rem;
    overflow-x: scroll;
    overflow-y: hidden;
    scroll-snap-type: x mandatory;
    cursor: grab;
    user-select: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }

    &.is-active {
      cursor: grabbing;
    };

    &::-webkit-scrollbar {
      display: none;
    }
  }

  // Article - override object
  &__article {
    background-color: transparent;

    .is-active & {
      pointer-events: none;
    }

    .o-article-thumbnail {
      &__image-container {
        padding-bottom: 122.5%;
      }

      &__content {
        display: flex;
        flex-direction: column;
      }

      &__title {
        order: 1;
        margin-top: 1.8125rem;
      }

      &__info {
        order: 3;
      }

      &__desc {
        order: 2;
        margin-top: 0.75rem;
      }
    }
  }

  // CTA
  &__cta-container {
    padding: 0 $s-gutter--edge-mobile;
  }

  /*** Media Queries ***/

  @include t-media-query($s-small-up) {
    &__articles {
      column-gap: 1.5938rem;
    }
  }

  @include t-media-query($s-medium-up) {
    padding: 4.5rem #{'min(6%, 7.5rem)'} 7.5rem;

    &__inner {
      max-width: 110rem;
      margin: 0 auto;
    }

    &__title {
      margin: 0 0 2.5125rem;
      text-align: center;
    }

    &__articles {
      grid-template-columns: repeat(3, 1fr);
      column-gap: 2.5rem;
      padding: 0;
      margin-bottom: 2.9625rem;
      overflow: visible;
      cursor: initial;
      user-select: initial;
    }

    &__cta-container {
      text-align: center;
    }
  }

  @include t-media-query($s-xlarge-up) {
    padding: 7.5rem #{'min(6%, 7.5rem)'} 12.625rem;

    &__title {
      margin-bottom: 4.1875rem;
    }

    &__articles {
      margin-bottom: 4.9375rem;
    }

    .o-article-thumbnail {
      &__title {
        margin-top: 2.625rem;
      }

      &__info {
        margin-top: 2.75rem;
      }

      &__desc {
        margin-top: 1.0625rem;
      }
    }
  }
}
